.prism-border {
  border-bottom: #005285 solid 1px; }

.pink-border {
  border: solid 1px pink; }

.orange-border {
  border: solid 1px orange; }

.teal-border {
  border: solid 1px teal; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

.label-text {
  color: #D4D4D4;
  font-size: 15px;
  text-align: left; }

.text-inner-shadow {
  background-color: #565656;
  color: transparent;
  text-shadow: 0px 2px 3px rgba(255, 255, 255, 0.5);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text; }

@keyframes opacity-animation {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.frame-box-shadow {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5); }

.priority-drop-menu-wrapper {
  display: flex;
  width: 258px;
  height: 138px;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  background-color: white; }

.heading {
  height: 54px;
  font-size: 1.4em;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../svg/drop_menu_wht.svg");
  background-repeat: no-repeat;
  color: #0C75B7; }

.animated-text {
  opacity: 1;
  animation-name: opacity-animation;
  animation-duration: 2s; }

.three-star-buttons {
  display: flex;
  height: 100px;
  flex-direction: row;
  justify-content: space-evenly;
  border-left: #005285 solid 1px;
  border-right: #005285 solid 1px;
  border-bottom: #005285 solid 1px;
  border-radius: 0px 0px 10px 10px; }

.star-button {
  width: 72px;
  height: 72px;
  background-image: url("../svg/star_big_off.svg");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #5d5d5d;
  font-size: .7em;
  letter-spacing: 0px;
  border: none;
  background-color: transparent;
  transition: background-image .5s;
  -webkit-transition: background-image .5s; }

.star-button:hover {
  background-image: url("../svg/star_big_on.svg");
  color: #005285;
  text-shadow: 0px 2px 3px rgba(255, 255, 255, 0.5);
  transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in; }

.bg-star-active {
  background-image: url("../svg/star_big_on.svg"); }
