.prism-border {
  border-bottom: #005285 solid 1px; }

.pink-border {
  border: solid 1px pink; }

.orange-border {
  border: solid 1px orange; }

.teal-border {
  border: solid 1px teal; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

.label-text {
  color: #D4D4D4;
  font-size: 15px;
  text-align: left; }

.text-inner-shadow {
  background-color: #565656;
  color: transparent;
  text-shadow: 0px 2px 3px rgba(255, 255, 255, 0.5);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text; }

@keyframes opacity-animation {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.frame-box-shadow {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5); }

.task-card-wrapper {
  background-color: rgba(255, 255, 255, 0.2);
  background-repeat: no-repeat;
  margin: 35px 0px 35px 0px;
  width: 720px;
  height: 128px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: #979797 solid 1px;
  border-radius: 10px; }

.task-first-column {
  padding: 5px 0px 0px 15px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-start; }

.process-name {
  margin: 0px 0px 0px 0px;
  color: #5d5d5d;
  font-weight: 400;
  border-bottom: #D8D8D8 solid 1px; }

.task-second-column {
  border-left: #979797 solid 1px;
  z-index: 0;
  width: 168px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  text-align: center; }

.play-pause {
  position: absolute;
  margin-top: 46px;
  margin-left: -19px; }

.task-play-icon {
  display: flex;
  width: 36px;
  height: 36px;
  background-image: url("../svg/play.svg");
  border: none;
  background-color: transparent; }

.task-stop-icon {
  display: flex;
  width: 36px;
  height: 36px;
  background-image: url("../svg/pause_process.svg");
  border: none;
  background-color: transparent; }

.workers-assigned {
  margin: 5px 0px 5px 0px;
  color: #012210;
  font-weight: 300; }

.workers-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center; }

.plus-minus {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.workers-number {
  color: #012210;
  font-weight: 600;
  font-size: 1.3em; }

.assigned-status-wrapper {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  background-color: #005285;
  border-radius: 0px 0px 10px 0px; }

.assigned-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  color: #ffEfff;
  font-size: 1.2em;
  opacity: 1;
  animation-name: opacity-animation;
  animation-duration: 2s; }

.show-priority-button {
  border: none;
  background-color: transparent; }

.set-priority-menu {
  position: absolute;
  margin-left: 500px;
  margin-top: 136px;
  z-index: 5; }

.details-wrapper {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: flex-end; }

.details-bar-bar {
  z-index: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 5px;
  opacity: 1;
  animation-name: opacity-animation;
  animation-duration: 2s; }

.progress-blur {
  width: 466px;
  height: 30px;
  display: flex;
  flex-direction: row;
  border-radius: 4px 4px 10px 4px;
  margin-left: 5px;
  background-image: linear-gradient(to right, #005285, #D0EDFF);
  font-size: .8em;
  font-weight: 400;
  text-align: left;
  line-height: .9em; }
